<template>
  <div class="declaration collection">
    <div class="rg-top-top">
      <div :class="path==1?'top-div-active top-div':'top-div'" @click="pathClick(1)">1、收入及减除项填写</div>
      <div class="top-arrow">&gt;&gt;</div>
      <div :class="path==2?'top-div-active top-div':'top-div'" @click="pathClick(2)">2、税款计算</div>
      <div class="top-arrow">&gt;&gt;</div>
      <div :class="path==3?'top-div-active top-div':'top-div'" @click="pathClick(3)">3、附表填写</div>
      <div class="top-arrow">&gt;&gt;</div>
      <div :class="path==4?'top-div-active top-div':'top-div'" @click="pathClick(4)">4、申报表报送</div>
    </div>
    <router-view @pathNumber="setActive"></router-view>
  </div>
</template>

<script>
export default {
  components: {},
  data () {
    return {
      allIncomeTable: [], // 综合所得预扣预缴表
      path: 1
    }
  },
  created () {
    this.$emit('router', this.$router.history.current.name)
  },
  mounted () {

  },
  methods: {
    submitForm (formName) {
      console.log(this.ruleForm.personStatus)
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    showNull () {
      this.$alert('本系统为教学系统，目前只提供常用操作功能噢', '信息', {
        confirmButtonText: '确定'
      })
    },
    nullClick () {
      this.showNull()
    },
    pathClick (number) {
      var _this = this
      switch (number) {
        case 1 :
          _this.$router.push('/declaration/allDeclaration')
          break
        case 2 :
          _this.$router.push('/declaration/taxCalculation')
          break
        case 3 :
          _this.$router.push('/declaration/extraTable')
          break
        case 4 :
          _this.$router.push('/declaration/application')
          break
      }
    },
    setActive (number) {
      this.path = number
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .top-arrow {
    margin: 0 5px;
  }
  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: block;
    margin: 0;
  }

  a {
    color: #42b983;
  }

  img {
    vertical-align: middle;
  }

  .declaration {
    text-align: start;
    padding: 0 20px;
  }

  .hot-notice {
    border: solid 1px #d2e7fb;
    background-color: #eaf4ff;
    box-sizing: border-box;
    margin: 10px 35px 0 10px;
    padding: 8px;
    line-height: 20px;
    text-align: left;
  }

  .rg-top-top {
    height: 40px;
    overflow: hidden;
    padding: 0 10px;
    line-height: 40px;
    box-sizing: border-box;
  }

  .rg-top-bottom {
    height: 40px;
    padding: 0 10px;
    line-height: 30px;
    font-size: 16px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
  }

  .right-btn {
    padding: 8px 10px 8px 10px;
    border: 1px #5a98de solid;
    border-radius: 5px;
    font-size: 14px;
  }

  .right-btn:hover {
    background-color: rgb(64, 158, 255);
    color: white;
  }

  .rg-top-top {
    height: 40px;
    overflow: hidden;
    line-height: 40px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    margin-top: 10px;
  }

  .top-div {
    flex: 1;
    background-color: #f4f6f9;
    color: #666666;
    text-align: center;
    border-radius: 3px;
    cursor: pointer;
  }

  .con_btn1 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid #4586f5;
    border-width: 0px 0px 2px 0px;
    font-size: 15px;
    color: #4586f5;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .con_btn2 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid transparent;
    border-width: 0 0 2px 0;
    font-size: 15px;
    color: #000000;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .in-table .notice {
    display: block;
    width: 100%;
    height: 20px;
    /* display: block; */
    background-color: rgb(212, 244, 252);
  }

  .in-table-content {
    padding: 0 30px;
  }

  .rg-top .el-form-item {
    margin: 10px 0;
  }

  .top-div-active {
    background-color: #4285F4 !important;
    color: #ffffff !important;
  }

</style>
<style>
  .el-button + .el-button {
    margin: 0;
  }

  .el-dialog__body {
    padding: 0;
  }

  .person-status .el-form-item__label {
    position: relative;
    float: right;
    right: 180px;
  }

  .person-status.el-form-item {
    margin-bottom: 5px;
  }

  [data-v-130d4e25] .el-radio-group {
    float: right;
    position: relative;
    right: -140px;
  }

  [data-v-130d4e25] .el-radio__input {
    display: none;
  }

  [data-v-130d4e25] .el-radio.el-radio--medium {
    margin-right: 10px;
  }

  .el-form-item__label {
    width: 120px;
  }

  .el-select {
    width: 100%;
  }

  .el-form-item__label {
    font-size: 12px;
  }
</style>



